body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
}

.container {
  padding: 0;
  box-shadow: 0 0 5px #888;
}

.profession {
  background: #000;
  color: #fff;
  font-size: 1.3rem;
}

.card-collapse {
  width: 30%;
  padding: 10px;
  color: #000;
}

.home {
  background: #CDCDCD;
}

.portfolio {
  background: #CDCDCD;
}

.capture {
  background: #CDCDCD;
}

.contact {
  background: #CDCDCD;
}

.card-collapse:hover {
  opacity: 0.8;
  cursor: pointer;
}

.card-collapse .fas {
  font-size: 2em;
}

.card-collapse p {
  padding-top: 2px;
  letter-spacing: 1.5px;
}


@media only screen and (min-width: 1199px) {
  .card-collapse {
    padding: 2em;
  }
}

@media only screen and (max-width: 550px) {
  .profession {
    font-size: 1em;
  }
}
.Nameheader {
  background: #252624;
  text-align: center;
}

@font-face {
  font-family: 'leaguespartan-bold';
  font-style: normal;
  font-weight: normal;
  src: local('leaguespartan-bold'), url(/me/static/media/leaguespartan-bold.de124ab4.woff) format('woff');
  }

 h1 {
  font-family: 'leaguespartan-bold';
  letter-spacing: 5px;
  font-size: 22px;
}

.Nameheader {
  position: relative;
}

.Nameheader p {
  padding-top: 50px;
  font-size: 20px;
}

.account_links {
  position: absolute;
  top: 15px; 
  right: 55px;
} 

.account_links a:nth-child(1) {
  color: #0077B5;
}

.account_links a:nth-child(2) {
  margin-left: 20px;
  color: #fff;
}

.account_links a:nth-child(3) {
  margin-left: 20px;
  color: #1DA1F2
}


@media only screen and (min-width: 550px) {

  h1 {
    font-size: 43px;
  }

  span h1 {
    display: inline-block;
    position: relative;  
  }
  span h1:before,
  span h1:after {
    content: "";
    position: absolute;
    border-bottom: 2px solid white;
    top: 20px;
    width: 70px;
  }
  span h1:before {
    right: 100%;
    margin-right: 15px;
  }
  span h1:after {
    left: 100%;
    margin-left: 15px;
  }
}
#homeCollapse p.lead {
  font-size: 15px;
  letter-spacing: 1.5px;
  margin-top: -25px;
}

#homeCollapse .card-background {
  background: #34515E;
}

#homeCollapse .card-second-background {
  background: #F2F1ED;
  font-family: 'Montserrat', sans-serif;
  letter-spacing: 1px;
  text-align: center;
}

#homeCollapse .card-second-background p,
i.fa-smile, i.fa-trophy {
  margin-bottom: 30px;
}

i.fa-smile, i.fa-trophy, i.fa-frown {
  color: orangered;
  font-size: 20px;
}

h6 {
  letter-spacing: 0.5px;
}


@media only screen and (min-width: 993px) {
  #homeCollapse p.lead {
    font-family: 'Space Mono', monospace;
    letter-spacing: 1.5px;
    margin-top: -25px;
  }

  h6 {
    letter-spacing: 5px;
  }

  #homeCollapse h6.second {
    padding-left: 88px;
  }
  
  #homeCollapse h6.third {
    padding-left: 277px;
  }
  
  #homeCollapse h6.fourth {
    padding-left: 450px;
  }
}
#portfolioCollapse .card-background {
  background: #7D314C;
}

#portfolioCollapse h1 + p {
  letter-spacing: 2px;
}

.what_have_i_build_header {
  padding-left: 57px;
  letter-spacing: 1px;
}

#portfolioCollapse .card-second-background {
  background: #F2F1ED;
}

.campImage{
  width: 315px;
  height: 250px;
}

img.campImage_full {
  width: 100%;
}

.imageFlex-Card {
  width: 315px;
  height: 100%;
  margin: 10px;
}

.imageFlex {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.card-body h4 a i.fa-github {
